body{
  margin: 0;
  background-color: whitesmoke;
}

header{
  height: 100px;
  display: flex;
  align-items: center;
  background: rebeccapurple;
}
header img{
  height: 80%;
  margin-left: 10%;
}
header p{
  font-family: VT323, monospace;
  color: whitesmoke;
  font-size: 50px;
  margin-left: 60px;
}
.meme{
  position: relative;
  width: 90%;
  margin: auto;
}
.meme img{
  width: 100%;
}
.meme h2{
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow: 
    2px 2px 0 #000,
    -2px -2px 0 #000,
    2px -2px 0 #000,
    -2px 2px 0 #000,
    0 2px 0 #000,
    2px 0 0 #000,
    0 -2px 0 #000,
    -2px 0 0 #000,
    2px 2px 5px #000;
}
.meme .bottom{
  bottom: 0;
}
.meme .top{
  top: 0;
}
.meme-form button{
  margin: 10px;
  border: none;
  font-family: VT323, monospace;
  font-size: 25px;
  letter-spacing: 1.5px;
  color: white;
  background: rebeccapurple;
}
.meme-form input::-webkit-input-placeholder{
  font-family: VT323, monospace;
  font-size: 15px;
  text-align: center;
} 